<template>
  <v-navigation-drawer
    class="main-sidebar"
    v-model="drawer"
    permanent
    :mini-variant.sync="mini"
  >
    <v-list-item class="mx-auto">
      <v-list-item-title class="text-center sidebar-brand font-weight-bold">{{
        title
      }}</v-list-item-title>
    </v-list-item>
    <v-list
      v-for="(section, key) in sidebarMenu"
      :key="key"
      class="py-0"
      :nav="nav"
    >
      <v-list-item class="min-h-34px" :class="{ 'd-none': mini }">
        <v-list-item-title class="d-flex align-center menu-header grey--text">{{
          section.name
        }}</v-list-item-title>
      </v-list-item>
      <template v-for="item in section.items">
        <v-list-group
          v-if="item.subitems"
          class="icon-mx-auto grey-2--text"
          :class="{ 'font-18': mini, 'font-13': !mini }"
          :prepend-icon="item.icon"
          v-model="item.model"
          :key="item.id"
        >
          <template v-slot:activator>
            <v-list-item-title
              class="nunito letter-spacing-dot-3 grey-2--text"
              >{{ item.name }}</v-list-item-title
            >
          </template>
          <v-list-item
            v-for="(subitem, skey) in item.subitems"
            :key="skey"
            link
          >
            <v-list-item-title
              class="font-13 nunito grey-2--text"
              :class="{ 'pl-65px': !mini }"
              >{{ subitem.name }}</v-list-item-title
            >
          </v-list-item>
        </v-list-group>
        <v-list-item link v-else class="min-h-34px" :key="item.id">
          <v-list-item-icon>
            <v-icon
              class="mx-auto"
              :class="{ 'font-18': mini, 'font-13': !mini }"
              >{{ item.icon }}</v-icon
            >
          </v-list-item-icon>
          <v-list-item-title class="nunito letter-spacing-dot-3 grey-2--text">{{
            item.name
          }}</v-list-item-title>
        </v-list-item>
      </template>
    </v-list>
    <v-list class="py-0" :nav="nav">
      <v-list-item class="min-h-34px my-4 py-3">
        <v-btn class="custom--btn" color="primary" block>
          <v-icon left :class="{ 'mx-auto': mini, 'mr-4': !mini }">
            fas fa-rocket
          </v-icon>
          {{ docbutton }}
        </v-btn>
      </v-list-item>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
import { sidebarMenu } from "../../constants/sidebar";

export default {
  name: "SideBar",
  data() {
    return {
      drawer: true,
      sidebarMenu,
    };
  },
  props: {
    mini: {
      type: Boolean,
      default: true,
    },
  },
  computed: {
    title: {
      get() {
        if (!this.mini) return "Stisla";
        return "St";
      },
    },
    nav: {
      get() {
        if (!this.mini) return false;
        return true;
      },
    },
    docbutton: {
      get() {
        if (!this.mini) return "Documentation";
        return "";
      },
    },
  },
};
</script>

<style lang="scss">
.v-list-item__group .v-list-item--link {
  min-height: 35px;
  height: 35px;
}

.v-list-item__title {
  margin-top: 0;
  margin-bottom: 0;
}

.custom--btn {
  text-transform: capitalize;
  width: 100%;
  font-size: 12px !important;
}

.dropdown-title {
  text-transform: uppercase;
  font-size: 10px;
  letter-spacing: 1.5px;
  font-weight: 700;
  color: #191d21 !important;
  padding: 10px 20px;
  line-height: 20px;
}
</style>
